<template>
  <div class="product-wrapper">

    <div class="list ">
      <p class="title">{{productInfo.name}}</p>
      <div class="company-logo">
        <img :src="baseSrc+productInfo.brandLogo" alt="公司logo">
      </div>
      <p class="condition">
        <span class="condition-item" v-for="(item,index) in productInfo.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>
      </p>

      <p class="product-ts">
        <span class="ts-item" v-if="productInfo.feature1"><i></i>{{productInfo.feature1}}</span>
        <span class="ts-item" v-if="productInfo.feature2"><i></i>{{productInfo.feature2}}</span>
        <span class="ts-item" v-if="productInfo.feature3"><i></i>{{productInfo.feature3}}</span>
      </p>
      <div class="characteristics" v-if="productInfo.parameter_values">
        <div class="character " v-for="(item,index) in productInfo.parameter_values[0].entries" :key="index">
          <span>{{item.name}}</span>
          <span>{{item.limit}}</span>
        </div>
      </div>
      <div class="price-btn">
        <div class="price">{{productInfo.price}}<span>元起</span></div>
        <div class="btn" @click="goDetail(productInfo.id)">查看详情</div>
      </div>
      <slot></slot>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    productInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      baseSrc: '',
      parameters: []
    }
  },
  mounted () {

    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    // if (this.productInfo.parameter_values === null) return
    // this.parameters = this.productInfo.parameter_values[0]
  },
  computed: {
    comLeftWidth () {
      return {
        width: this.leftWidth ? this.leftWidth + 'px' : '800px'
      }
    },
    comRightWidth () {
      return {
        width: this.rightWidth ? this.rightWidth + 'px' : '800px'
      }
    }
  },
  methods: {
    goDetail (id) {
      this.$router.push({
        path: `/product/detail/${id}`,
        query: {
          productId: id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped="scoped">
  @import '@/assets/styles/mixin.scss';

  .product-wrapper {
    .list {
      position: relative;
      margin-bottom: pxToRem(20px);
      background: #FFFFFF;
      padding: pxToRem(29px) 0 0 pxToRem(30px);
      .title {
        color: $color-text;
        font-weight: bold;
        font-size: pxToRem(36px);
        width: pxToRem(560px);
        line-height: pxToRem(42px);
      }
      .condition {
        margin: pxToRem(30px) 0;
        padding-right: pxToRem(30px);
        .condition-item {
          font-size: pxToRem(24px);
          color: #A5A8A8;
          &:not(:last-child):after {
            content: '|';
            padding: 0 pxToRem(10px);
          }
        }
      }
      .product-ts {
        font-size: 0;
        display: flex;
        flex-wrap: wrap;
        .ts-item {
          font-size: pxToRem(28px);
          color: $color-text;
          margin-right: pxToRem(50px);
          margin-bottom: pxToRem(20px);
          i {
            display: inline-block;
            vertical-align: top;
            margin-top: pxToRem(-2px);
            background: url("../../assets/images/icon_ts.png") no-repeat;
            background-size: pxToRem(28px) pxToRem(28px);
            width: pxToRem(28px);
            height: pxToRem(28px);
            margin-right: pxToRem(12px);
          }
        }

      }
      .company-logo {
        position: absolute;
        right: pxToRem(30px);
        top: pxToRem(30px);
        width: pxToRem(110px);
        height: pxToRem(60px);
        overflow: hidden;
        img {
          width: pxToRem(110px);
          height: pxToRem(60px);
        }
      }
      .characteristics {
        padding-right: pxToRem(30px);
        margin-top: pxToRem(10px);
        border-bottom: $border-bottom-light-color;
        padding-bottom: pxToRem(10px);
        .character {
          display: flex;
          justify-content: space-between;
          font-size: pxToRem(28px);
          margin-bottom: pxToRem(20px);
          color: #A5A8A8;
        }
      }
      .logo {
        position: absolute;
        right: pxToRem(30px);
        top: pxToRem(30px);
        width: pxToRem(110px);
        height: pxToRem(60px);
        overflow: hidden;
        img {
          width: pxToRem(110px);
          height: pxToRem(60px);
        }
      }
      .price-btn {
        display: flex;
        justify-content: space-between;
        height: pxToRem(100px);
        line-height: pxToRem(100px);
        align-items: center;
        padding-right: pxToRem(30px);
        .price {
          font-size: pxToRem(40px);
          color: $color-active-text;

          span {
            font-size: pxToRem(24px);
            color: #D5D8D8;
          }
        }
        .btn {
          font-size: pxToRem(24px);
          background: $color-active-text;
          border-radius: pxToRem(34px);
          height: pxToRem(60px);
          line-height: pxToRem(60px);
          width: pxToRem(150px);
          text-align: center;
          color: #fff;
        }
      }
      .part-rt {
        position: relative;
        overflow: hidden;
        float: right;
        background-color: rgba(241, 244, 244, .5);
        height: 100%;
        width: 260px;
      }
    }
  }
</style>
