<template>
  <div>
    <div class="page-infinite-wrapper" ref="wrapper">
      <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
        <li v-for="(item,index) in productList" :key="index">
          <product :productInfo="item">
          </product>
        </li>
      </ul>
      <p v-show="loading&&productList.length" class="page-infinite-loading">
        <mt-spinner type="fading-circle"></mt-spinner>
        加载中...
      </p>
      <p v-show="!loading&&productList.length" class="noMoreData">没有更多数据了</p>
      <p v-show="!productList.length" class="noProduct">该分类下暂无产品</p>
    </div>
  </div>
</template>

<script>

import product from '@/components/product/BaseProduct'

export default {
  name: 'ProductList',
  data () {
    return {
      page: 1,
      loading: false,
      limit: 10,
      productCategoryId: '',
      productList: []
    }
  },
  mounted () {
    if (this.$route.query.productCategoryId) {
      this.productCategoryId = this.$route.query.productCategoryId
    } else {
      this.productCategoryId = '5'
    }
    this.getList()
  },
  methods: {
    getList () {
      return new Promise((resolve, reject) => {
        this.$api.product.getProductListWidthType({
          productCategoryId: this.productCategoryId,
          page: this.page,
          limit: this.limit
        }).then(res => {
          this.loading = true
          this.productList = res.data
          this._checkMore(res)
        })
      })
    },
    loadMore () {
      if (!this.loading) {
        return
      }
      this.page++
      this.$api.getProductListWidthType.getProductListWidthType({
        page: this.page,
        limit: this.limit,
        productCategoryId: this.productCategoryId
        // status: this.status
      }).then(
        function (res) {
          // this.$set(this.lists, `status${this.status}`, res.data.dataGrid.data)\
          this.productList = this.productList.concat(res.data)
          this._checkMore(res)
        }.bind(this)
      )
        .catch(function (err) {
        })
    },
    _checkMore (data) {
      if (!data.data.length || (this.page * this.limit) >= data.count) {
        this.loading = false
      }
    }
  },
  components: {
    product
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';
  .page-infinite-loading{
    text-align: center;
  }
  .mint-spinner-fading-circle{
    margin:0 auto;
  }
  .noMoreData,.noProduct{
    color: #A5A8A8;
    text-align: center;
    margin-top: pxToRem(30px);
  }
  .page-infinite-listitem {
    margin-bottom: pxToRem(20px);
  }
</style>
